import { Link } from 'react-router-dom';
import MyButton from '../myButton/MyButton';
import styles from './productCard.module.css'

interface IShopDataProps {
    id: number,
    title: string,
    price: number,
    description?: string,
    category?: string;
    image: string;
    rate?: number;
    count?: number;
    rating?: {
        rate: number
        count: number
    }
  } 

function ProductCard({id, title, price, image, rate, count, rating}: IShopDataProps) {
  return (
    <div key={id} className={styles.card}>
      <h5>{title.length > 20 ? title.substring(0,20)+ '...' : title}</h5>
      <p>{price} €</p>
      <div className={styles.imgWrapper}>
      <img width={200} src={image} alt={title} />      
      </div>
      {/* <span> Rating: {rate} </span> */}
      <Link to={String(id)}><MyButton name='about product' /></Link>
      
    </div>
  )
}

export default ProductCard;
