import { HashRouter, Route, Routes } from 'react-router-dom';
import Layout from './components/layout/Layout';
import StarWarsGallery from './components/StarWarsGallery/StarWarsGallery';
import FormGender from './components/formGender/FormGender';
import RobotForm from './components/robotForm/RobotForm';
import MyForm from './components/myForm/MyForm';
import MyButton from './components/myButton/MyButton';
import LoginForm from './components/loginForm/LoginForm';
import HomePage from './components/homePage/HomePage';
import Shop from './components/shop/Shop';
import ProductPage from './components/productPage/ProductPage';
import Login from './components/login/Login';
import { Provider } from 'react-redux';
import { store } from './redux/store';
import ProtectedRoute from './components/protectedRoute/ProtectedRoute';


const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <Provider store={store}>
  <HashRouter>
    <Routes>
      <Route path='/' element={<Layout />}>
        <Route path='/home' element={<HomePage />} />
        <Route path='/login-form' element ={<LoginForm />} />
        <Route path='/shop' element ={<ProtectedRoute outlet={<Shop />}/>} />
        <Route path='/shop/:id' element = {<ProtectedRoute outlet={<ProductPage />}/>} />
        <Route path='/login' element ={<Login />} />
        <Route path='/star-wars' element={<ProtectedRoute outlet={<StarWarsGallery />}/>} />
        <Route path='/gender-form' element ={<ProtectedRoute outlet={<FormGender />}/>} />
        <Route path='/robot-form' element ={<ProtectedRoute outlet={<RobotForm />}/>} />
        <Route path='/my-form' element = {<ProtectedRoute outlet={<MyForm />}/>} />
        <Route path='/my-button' element = {<ProtectedRoute outlet={<MyButton />}/>} />
        {/* <Route path='/hero-card' element ={<HeroCard />} /> */}
        <Route path='*' element ={<h1>Error 404</h1>} />
        {/* <App /> */}
        {/* <Lesson06 />
        <Lesson07 /> */}
        {/* <Layout /> */}
        {/* <StarWarsGallery /> */}
        {/* <Lesson09 /> */}
        {/* <CatFetch /> */}
        {/* <Lesson11 />
        <Lesson12 /> */}
        {/* <Lesson13 /> */}
      </Route>
    </Routes>
  </HashRouter>
  </Provider>
);

